@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500;700&family=Roboto+Mono:wght@100;300;400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header{width:100%;display:flex;align-items:center;height:100px}.header a{margin:0 28px;text-decoration:none;color:#181818;font-size:12px;font-weight:600;font-family:'Raleway', sans-serif}.container{position:relative;color:#181818;width:1280px;min-width:1280px;margin:0 auto;display:flex;justify-content:center}.container .page{position:absolute}.container .page .inner{display:flex;justify-content:center;height:90vh}.container .page .inner h1{font-family:'Raleway', sans-serif;font-size:62px;font-weight:800;letter-spacing:0.08rem}.container .page .inner h1 .line-wrap{overflow:hidden;height:75px}.container .page .inner p{font-family:'Roboto Mono', monospace;margin-top:200px;width:340px;font-weight:300;line-height:26px;font-size:14px}.container .page .inner button{margin-top:20px;width:170px;height:50px;background:none;font-family:'Roboto Mono', monospace;font-size:14px;cursor:pointer}.container .page .inner button:hover{background-color:white;color:#1b1b1c;height:55px}.container .page .inner .black{background-color:#1b1b1c;color:white}.page-enter{opacity:0}.page-enter-active{opacity:1;transition:opacity 400ms;transition-delay:600ms}.page-exit{opacity:1}.page-exit-active{opacity:0;transition:opacity 400ms}

.main{background-color:red}.main p{font-weight:300;line-height:26px;font-size:14px}.main .title{margin:20px;display:flex;flex-direction:column;font-family:'Raleway', sans-serif}.main .title h2{font-size:120px;color:#ffe190}.main .grid{display:grid;color:#1b1b1c;padding:2px;grid-template-columns:repeat(3, 1fr);grid-template-rows:repeat(2, 1fr);grid-gap:2px;gap:2px;justify-content:center;font-family:'Roboto Mono', monospace}.main .grid .project{background-color:white;padding:10px}.main .grid .project img:hover{opacity:.5;transition-duration:.6s}.main .grid .project .image{height:50%;width:100%;object-fit:cover}.title{margin:20px;display:flex;flex-direction:column;font-family:'Raleway', sans-serif}.title h2{font-size:120px;color:#ffe190;margin-bottom:10px}.title button{width:350px;height:50px;background:none;font-family:'Roboto Mono', monospace;font-size:16px;cursor:pointer;background-color:#1b1b1c;color:white;margin:20px}.title button:hover{color:#1b1b1c;background-color:white;height:55px}.visible,.invisible{opacity:0.0;transition:opacity 0.5s ease}.visible{opacity:1.0}

